.contact-form{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 80vh;
    margin-top: 4rem;
      /* scroll */
  padding-top: 3rem;
}

.c-right{
    display: flex;
    justify-content: center;
    position: relative;
    flex: 1;
}

.c-right>form
{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
    
}

.c-right .user{
    width: 20rem;
    height: 2rem;
    padding: 0.3em;
    outline: none;
    border: 2px solid var(--orange);
    border-radius: 8px;
    font-size: 16px;   
}

textarea{
    height: 4rem!important;
}

.blur{
    position: absolute;
    border-radius: 50%;
    filter: blur(72px);
    z-index: -9;
}

.c-purple{
    background: #edd0ff;
    width: 29rem;
    height: 14rem;
    top: 3rem;
    right: 8rem;
}
.c-skyblue{
    background: #C1F5FF;
    width: 21rem;
    height: 11rem;
    top: 1rem;
    left: 20rem;
}

@media screen and (max-width: 480px) {
    .contact-form{
        padding: 0;
        flex-direction: column;
        gap: 5rem;
        height: 40rem;
    }
    .c-right .user{
        width: 16rem;
    }

}   