.experience{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5rem;
    height: 30vh;
    margin-bottom: 8rem;
    position: relative;
}

.achievement{
    display: flex;
    align-items: center;
    flex-direction: column;
}

.circle{
    width: 4rem;
    height: 4rem;
    background: white;
    border-radius: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 2rem;
}

.circle::before {
    border-radius: 100%;
    content: '';
    background-image: linear-gradient(to bottom, #87e6fb 0%, #ffc05c 100%);
    top: -8px;
    left: -8px;
    bottom: -8px;
    right: -8px;
    position: absolute;
    z-index:-1;
    box-shadow: var(--boxShadow);
}

.achievement span:nth-of-type(2){
    color: var(--orange);
}

/* blur */

.blur{
    position: absolute;
    border-radius: 50%;
    filter: blur(72px);
    z-index: -9;
}

.blur.purple{
    background: #edd0ff;
    width: 22rem;
    height: 14rem;
    left: 44rem;
    top: 0rem;
}

@media screen and (max-width: 480px) {
    .experience{
        transform: scale(0.7);
    }
}