.works{
    padding: 2rem 3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-top: 4rem;
}

/* right side */

.w-right {
    position: relative;
}

.w-mainCircle{
    left: 9rem;
    position: relative;
    width: 18rem;
    height: 18rem;
    border-radius: 100%;
    box-shadow: var(--smboxShadow);
    top: 2rem;
    background: white;
    z-index: 2;
}

.w-mainCircle>*{
    position: absolute;
}

/* Flutter */
.w-mainCircle > :nth-child(1){
    top: -3rem;
    left: 5.5rem;
}

/* ReactJs */
.w-mainCircle > :nth-child(2){
    top: 1.5rem;
    left: -2.5rem;
}

/* HtmlCssJs */
.w-mainCircle > :nth-child(3){
    top: 5.5rem;
    left: 5.8rem;
}

/* Python */
.w-mainCircle > :nth-child(4){
    top: 1rem;
    left: 13.5rem;
}

/* PhpMysql */
.w-mainCircle > :nth-child(5){
    top: 14rem;
    left: 6.3rem;
}

/* vue */
.w-mainCircle > :nth-child(6){
    top: 10rem;
    left: -2rem;
}

/* node */
.w-mainCircle > :nth-child(7){
    top: 9.5rem;
    left: 14rem;
}

.w-secCircle {
    width: 6rem;
    height: 6rem;
    position: absolute;
    border-radius: 100%;
    border: 5px solid #ebebeb;
    display: flex;
    align-items: center;
    justify-content: center;
    background: "none";
    box-shadow: var(--smboxShadow);
    background: white;
}
  
.w-secCircle > img {
  transform: scale(1);
  width: 100%;
  aspect-ratio: 3/2;
  object-fit: contain;
  border-radius: 50%;
}

.w-backCircle {
  position: absolute;
  height: 13rem;
  width: 13rem;
  border-radius: 100%;
  /* darkMode */
  z-index: 1;
}

.blueCircle > img{
    background: white;
    position: relative;
    left: 3rem;
    top: -2rem;
    transform: scale(0.5);
    /* width: 30%;
    height: 30%; */
    /* aspect-ratio: 3/2; */
    object-fit: contain;
    border-radius: 50%;
}

.yellowCircle > img{
    background: white;
    position: relative;
    left: 2rem;
    top: 2rem;
    transform: scale(0.5);
    width: 100%;
    height: 100%;
    /* aspect-ratio: 3/2; */
    object-fit: contain;
    border-radius: 50%;
}

.blueCircle {
  background: #1949b8;
  left: 18rem;
  top: 0rem;
}

.yellowCircle {
  /* background: #f5c32c; */
  background: #55d9ff;
  left: 18rem;
  top: 8rem;
}

@media screen and (max-width: 480px) {
    .works{
        flex-direction: column;
        height: 55rem;
        padding: 0;
        gap: 5rem;
    }
    .w-right{
        display: flex;
        align-items: flex-start;
        justify-content: start;
        transform: scale(0.7);
    }
    .w-mainCircle{
        position: static;
    }
    .w-backCircle{
        left: 8rem !important;
    }
}