.intro{
    display: flex;
    height: 77vh;
    margin-top: 6rem;
}
.i-left{
    display: flex;
    position: relative;
    flex: 1;
    flex-direction: column;
    gap: 2rem;
}

.i-name{
    display: flex;
    flex-direction: column;
}

.i-name>:nth-child(1){
    color: var(--black);
    font-weight: bold;
    font-size: 3rem;
}

.i-name>:nth-child(2), .typewrite .wrap{
    color: var(--orange);
    font-weight: bold;
    font-size: 3rem;
}

.typewrite-wrap{
    height: 3rem;
}

.i-name>:nth-child(3){
    font-weight: 100;
    font-size: 14px;
    color: var(--grey);
    margin-top: 1rem;
    
}

.i-button{
    width: 6rem;
    height: 2rem;
}

.i-icons{
    margin-top: 3rem;
    display: flex;
    gap: 1.5rem;
}

.i-icons>*{
    /* transform: scale(0.5);    */
    box-shadow: var(--btnShadow);
    border-radius: 15px;
    padding: 5px;
}

.i-icons>*{
    cursor: pointer;
    /* -webkit-filter : hue-rotate(160deg);
    filter : hue-rotate(160deg); */
}

.i-right{
    flex: 1;
    position: relative;
}

.i-right>*{
    position: absolute;
    z-index: 1;
}

.i-right>:nth-child(1){
    transform: scale(0.69);
    left: -15%;
    top: -9%;
}

.i-right>:nth-child(2){
    top: -0.1rem;
    transform: scale(0.95);
    left: 3rem;
    /* -webkit-filter : hue-rotate(160deg);
    filter : hue-rotate(160deg); */
}

/* .i-right>:nth-child(3){
    transform: scale(1.4);
    left: 28%;
} */

.i-right>:nth-child(3){
    transform: scale(0.55);
    left: 3%;
    top: -65%;
    filter: grayscale(100%);
    -webkit-filter: grayscale(100%);
}

.i-right>:nth-child(4){
    transform: scale(0.6);
    top: -19%;
    left: -24%;
    border-radius: 50%;
    padding: 0;
    /* -webkit-filter : hue-rotate(160deg);
    filter : hue-rotate(160deg); */
}

/* .i-right>:nth-child(5){
    -webkit-filter : hue-rotate(160deg);
    filter : hue-rotate(100deg);
} */

.blur{
    position: absolute;
    border-radius: 50%;
    filter: blur(72px);
    z-index: -9;
}

.blur.purple{
    background: #edd0ff;
    width: 22rem;
    height: 14rem;
    top: -18%;
    left: 56%;
}

.blur.purple2{
    background: #edd0ff;
    top: 17rem;
    width: 21rem;
    height: 11rem;
    left: -9rem;
}

.blur.skyblue{
    background: #C1F5FF;
    top: 17rem;
    width: 21rem;
    height: 11rem;
    left: -9rem;
}

.blur.skyblue2{
    background: #C1F5FF;
    width: 22rem;
    height: 14rem;
    top: -18%;
    left: 56%;
}

@media screen and (max-width: 480px) {
    .intro{
        flex-direction: column-reverse;
        height: 64rem;
        gap: 7rem;
    }
    .i-right{
        transform: scale(0.8);
        left: -5rem;
    }
    .i-right .blur{
        display: none;
    }
    .floating-div:nth-of-type(1){
        top: 1rem !important;
        left: 15rem !important;
    }
}

@media screen and (max-width: 768px) {
    .intro{
        flex-direction: column-reverse;
        height: 64rem;
        gap: 7rem;
    }
    .i-right{
        transform: scale(1.3);
        left: -1rem;
    }
    .i-right .blur{
        display: none;
    }
    .floating-div:nth-of-type(1){
        top: 1rem !important;
        /* left: 15rem !important; */
    }
}

