.scroll-button{
    position: fixed; 
    /* width: 100%; */
    left: 94%;
    bottom: 70px;
    height: 20px;
    font-size: 3rem;
    z-index: 1;
    cursor: pointer;
    color: #55d9ff;
    background: none;
    border: none;
}

.scroll-button-icon{
    height: 60px;
    width: 60px;
    box-shadow: 2px 5px 10px var(--orangeCard);
    border-radius: 50%;
    background: white;
    transition: transform 0.2s ease-in;
}

.scroll-button-icon:hover{
    transform: rotate(360deg);
    box-shadow: none;
}

@media screen and (max-width: 480px) {
    .scroll-button{
        transform: scale(0.6);
        left: 20rem;
    }   
}