.services{
    padding: 1rem 3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-bottom: 8rem;
    margin-top: 9rem;
}

.awesome{
    display: flex;
    position: relative;
    flex-direction: column;
    gap: 1;
}

.awesome > :nth-child(1){
    color: var(--black);
    font-size: 2.5rem;
    font-weight: bold;
}

.awesome > :nth-child(2){
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
}

.awesome > :nth-child(3){
    color: var(--grey);
    font-size: 14px;
    margin-top: 1rem;
}

.s-button{
    width: 8rem;
    height: 2rem;
    margin-top: 1rem;
}

.cards{
    position: relative;
    gap: 1;
}

.cards>*{
    position: absolute;
}

/* blur */

.blur{
    position: absolute;
    border-radius: 50%;
    filter: blur(72px);
    z-index: -9;
}

.blur.purple{
    background: #edd0ff;
    width: 22rem;
    height: 14rem;
    left: 14rem;
    top: 8rem;
}

.blur.skyblue{
    background: #C1F5FF;
    width: 21rem;
    height: 11rem;
    left: -18rem;
    top: 13rem;
}

@media screen and (max-width: 480px) {
    .services{
        margin-top: 0;
        flex-direction: column;
        gap: 5rem;
        height: 66rem;
        padding: 0;
    }
    .cards{
        display: flex;
        flex-direction: column;
        gap: 17rem;
    }

    .cards>*{
        position: static;
    }
}