.n-wrapper{
    height: 10vh;
    display: flex;
    justify-content: space-between;
}

.n-left{
    flex: 1;
    align-items: center;
    display: flex;
    gap: 2rem;
}

.n-name{
    font-size: 1.3rem;
    font-weight: bold;
}

.first-name{
    /* color: var(--orange); */
    color: #f53100;
}

.n-logo{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
}

.n-logo > img{
    width: 50px;
    height: 50px;
}

.logo-link{
    text-decoration: none;
    color: var(--black);
}

.n-right{
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    font-weight: 400;
}

.n-list{
    flex: 10;
}

.n-list > ul{
    display: flex;
    gap: 2rem;
    margin-right: 4rem;
    list-style-type: none;
    cursor: pointer;
}

.n-list > ul > li:hover, .n-list > ul > a:hover{
    cursor: pointer;
    color: var(--orange);
}

.n-button{
    flex: 2;
}

@media screen and (max-width: 480px) {
    .n-list{
        display: none;
    }
    .n-button{
        display: none;
    }
    .n-name{
        font-size: 1rem;
        font-weight: bold;
    }
}