@font-face {
  font-family: Fonarto;
  src: url(./fonts/Fonarto.ttf);
}
*, #root{
  font-family: Fonarto;
  font-weight: normal;
  font-size: 20px;
}
:root
{
    /* --yellow: #F5C32C;
    --orange : #FCA61F; */
    --yellow: #55d9ff;
    --orange : #3382e5db;
    --black : #242D49;
    --gray : #788097;
    --blueCard : #DDF8FE;
    --purple: rgb(238 210 255);
    --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard: rgba(31, 204, 252, 0.45);
    --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
    --btnShadow: 2px 5px 10px rgba(40, 230, 251, 0.42);
    --btnShadowInvert: 2px 5px 10px rgba(255, 250, 250, 0.973);
}

.App{
    padding: 0.5rem 3.5rem;
    overflow: hidden;
    color: var(--black);    
}

.button{
  border-radius: 34px;
  border: none;
  color: white;
  font-size: 16px;
  padding: 11px 26px 11px 26px;
  /* background: linear-gradient(180deg, #fdc50f 26.71%, #fb982f 99.36%); */
  background: linear-gradient(180deg, #55d9ff 26.71%, #3382e5db 99.36%);
  /* box-shadow: 0px 20px 24px 3px rgba(40, 230, 251, 0.42); */
  box-shadow: var(--btnShadow);

}

.button:hover{
  background: white;
  cursor: pointer;
  color: var(--orange);
  border: 1px solid var(--orange);
}

@media screen and (max-width: 480px) {
  .App{
    padding: 0.5rem 1rem;
  }
}
  

  